<template>
  <div>
    <div class="mailbox">
      <div v-if='status==0'>
        <div class="mail-box" v-if="user.mail.length>0">
          <span>{{ $t('profile.info.current_email') }}:</span>
          <span class="old-email">{{user.mail}}</span>
        </div>
        <FormEmailVerifyCodeByStep
          class="form-email-code"
          type="email"
          mode="dynamicVerifyEmail"
          ref="formEmailVerifyCode"
          :showCancelBtn="true"
          @cancel="handleClose"
          @validate="checkVerifyCode" />
      </div>
      <div v-show='status==1'>
        <el-form :model="passForm" :rules="passRules" ref="passForm" @submit.native.prevent
          @keyup.enter.native.stop="$act(confirBindEmail)">
          <el-form-item prop="password">
            <el-input
              v-model="passForm.password"
              :placeholder="$t('profile.info.please_enter_your_password')"
              type="password" show-password />
          </el-form-item>
          <div class="button-item">
            <el-button @click="handleClose">{{ $t('public.dialog.cancel') }}</el-button>
            <el-button
              type="primary"
              @click="$act(confirBindEmail)">{{ $t('public.dialog.confirm') }}
            </el-button>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import FormEmailVerifyCodeByStep from '@/components/form/whole-form/FormEmailVerifyCodeByStep';

import { ApiResetMail, checkVerificationCodeMatching } from '@/api/email';
import formValidation, { validateFormFields } from '@/utils/validators/rules-validation';

export default {
  dialogify: {
    'close-on-click-modal': false,
    width: '460px',
    center: true,
    size: 'medium',
  },
  components: {
    FormEmailVerifyCodeByStep,
  },
  props: {
    user: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      visible: false,
      status: 0,
      form: {
        email: '',
        verifyCode: '',
      },
      passForm: {
        password: '',
      },
      passRules: {
        password: [
          formValidation.validateRequirement(),
          formValidation.validateCharLength(6, 25),
        ],
      },
    };
  },
  methods: {
    /** @return { void } */
    handleClose() {
      this.status = 0;
      this.$emit('cancel');
    },
    /** @return { Promise<void> } */
    async checkVerifyCode(form) {
      if (form) {
        this.form = form;
      }
      try {
        await checkVerificationCodeMatching({ ...this.form, type: 5 });
        this.status = 1;
      } catch (err) {
        this.$errHandler(err);
      }
    },
    /** @return { Promise<boolean> } */
    async validateAsBool(form) {
      try {
        await form.validate();
        return true;
      } catch {
        return false;
      }
    },
    // 确认修改
    /** @return { Promise<void> } */
    async confirBindEmail() {
      if (await this.validateAsBool(this.$refs.passForm)) {
        await ApiResetMail({
          email: this.form.email.toLowerCase(),
          password: this.passForm.password,
          verifyCode: this.form.verifyCode,
        });
        this.status = 0;
        this.form.verifyCode = '';
        this.passForm.password = '';
        this.$message.success(i18n.t('profile.info.change_link_email_success'));
        this.$emit('confirm', this.form.email.toLowerCase());
        this.form.email = '';
      }
    },
  },
};
</script>

