<template>
  <el-form
    :model="company"
    :rules="rules"
    :disabled="!$can('system', 'OP0007')"
    ref="form"
    class="clearfix">
    <el-form-item prop="name" :label="$t('profile.info.full_company_name') + ':'">
      <el-input v-model="company.name" disabled />
    </el-form-item>

    <el-form-item prop="creatorFirstName"
      :label="$t('profile.info.applicant') + ':'" class="applicant-item">
      <el-input
        v-model="company.creatorFirstName"
        :placeholder="$t('profile.info.first_name')"
        @change="validateFormField('creatorFirstName', $event)"
        style="width: 200px;" />
    </el-form-item>
    <el-form-item prop="creatorLastName" class="applicant-item last">
      <el-input
        v-model="company.creatorLastName"
        :placeholder="$t('profile.info.last_name')"
        @change="validateFormField('creatorLastName', $event)"
        style="width: 200px;" />
    </el-form-item>

    <el-form-item prop="abbreviation"
      :label="$t('profile.info.company_alias') + ':'">
      <el-input
        v-model="company.abbreviation"
        :placeholder="$t('profile.info.company_alias')"
        @change="validateFormField('abbreviation', $event)" />
    </el-form-item>

    <el-form-item prop="creatorPosition"
      :label="$t('profile.info.position') + ':'">
      <el-input
        v-model="company.creatorPosition"
        :placeholder="$t('profile.info.position')"
        @change="validateFormField('creatorPosition', $event)" />
    </el-form-item>

    <el-form-item prop="type" :label="$t('profile.info.business_type') + ':'">
      <dictionary-selector
        :value="company.type"
        type="company"
        disabled
        class="form-select" />
    </el-form-item>

    <el-form-item prop="creatorPhone"
      :label="$t('profile.info.contact_info') + ':'">
      <el-input
        v-model="company.creatorPhone"
        :placeholder="$t('profile.info.phone_number')"
        @change="validateFormField('creatorPhone', $event)" />
    </el-form-item>

    <el-form-item prop="scale"
      :label="$t('profile.info.company_size') + ':'">
      <dictionary-selector
        :value="company.scale"
        type="scale"
        class="form-select"
        @on-change="validateFormField('scale', $event)" />
    </el-form-item>

    <el-form-item prop="creatorMail"
      :label="$t('profile.info.registration_email') + ':'">
      <el-input v-model="company.creatorMail" disabled />
    </el-form-item>

    <div class="address-box">
      <el-form-item prop="country"
        :label="$t('profile.info.address') + ':'">
        <!-- 国家 -->
        <DictionaryCountrySelector
          :value="company.country && String(company.country)"
          type="scale"
          class="form-select"
          @change="validateFormField('country', $event)" />
        <!-- 具体地址 -->
      </el-form-item>
      <el-form-item prop="address">
        <el-input
          v-model="company.address"
          :rows="3"
          maxlength="100"
          show-word-limit
          type="textarea"
          :placeholder="$t('profile.info.address')"
          resize="none"
          @change="validateFormField('address', $event)" />
      </el-form-item>
    </div>

    <el-form-item prop="industry" :label="$t('profile.info.industry') + ':'">
      <el-input
        v-model="company.industry"
        :rows="5"
        maxlength="200"
        show-word-limit
        type="textarea"
        :placeholder="$t('profile.info.business_desc')"
        resize="none"
        @change="validateFormField('industry', $event)" />
    </el-form-item>
  </el-form>
</template>

<script>
import DictionarySelector from '@/components/dictionary/DictionarySelector';
import DictionaryCountrySelector from '@/components/dictionary/DictionaryCountrySelector';

import { mixinValidateFormField } from '@/mixins/form-validation';
import validator from '@/utils/validators/rules-validation';

export default {
  components: {
    DictionarySelector,
    DictionaryCountrySelector,
  },
  props: {
    company: {
      type: Object,
      required: true,
    },
  },
  mixins: [mixinValidateFormField],
  data() {
    return {
      rules: {
        name: [
          validator.spaceTrim(),
          validator.charLengthMax(64),
        ],
        creatorFirstName: [
          validator.spaceTrim(),
          validator.charTypeNormal1(),
          validator.charLengthMax(20),
        ],
        creatorLastName: [
          validator.spaceTrim(),
          validator.charTypeNormal1(),
          validator.charLengthMax(20),
        ],
        abbreviation: [
          validator.spaceTrim(),
          ...validator.charLengthRange(4, 20),
        ],
        creatorPosition: [
          validator.spaceTrim(),
          validator.charLengthMax(64),
        ],
        creatorPhone: [
          validator.charLengthMax(64),
          validator.charTypeDigitAndHyphen(),
        ],
        address: [
          validator.spaceTrim(),
          validator.charLengthMax(100),
        ],
        industry: [
          validator.spaceTrim(),
          validator.charLengthMax(200),
        ],
      },
    };
  },
  methods: {

  },
};
</script>

