.el-form {
  // 公共样式
  .form-select {
    width: 100%;
  }
  .el-form-item,
  .address-box {
    float: left;
    width: 420px;
    margin: 0 120px 15px 0;
  }
  // 特殊表单布局
  .applicant-item {
    width: 200px;
    margin: 0 0 15px;
    &.last {
      margin: 40px 0 0 20px;
    }
  }

  /deep/ .el-form-item__label {
    float: none;
    font-size: 14px;
    font-weight: 400;
    color: #7f7f7f;
  }
  /deep/ .el-form-item__content {
    font-size: 16px;
  }
}
</style>
