import axios, { axiosEncrypt } from '@/utils/axios';

/* ======================================= 1.个人中心 ======================================= */
export const ApiCanUseShortName = (name) => {
  return axios({
    method: 'get',
    url: '/authcenter/user/account/check',
    params: { account: name },
  });
};

export const ApiUpdateCompany = (data) => {
  return axios({
    method: 'post',
    url: '/authcenter/company/update/info',
    data,
  });
};

export const ApiUserInfo = () => {
  return axios({
    method: 'get',
    url: '/authcenter/user/info/get',
  });
};

export const ApiUpdateUserInfo = (data) => {
  return axios({
    method: 'post',
    url: '/authcenter/user/account/update',
    data,
  });
};

export const ApiModifyPassword = (data) => {
  return axiosEncrypt({
    method: 'post',
    url: '/authcenter/user/pass/update',
    data,
  });
};

/* ======================================= 2.协同空间 ======================================= */


export const ApiUserWorkspaces = () => {
  return axios({
    method: 'get',
    url: '/sdtc_show/private/workspace/userWorkspaces',
  });
};

// 获取所有workspace的列表数据
export const ApiAllWorkpaceTable = () => {
  return axios({
    method: 'get',
    url: '/authcenter/app/users',
    // params: { appId: id },
  });
};

/* ======================================= 6.账号中心 ======================================= */

// 发送邮件
export const sendEmailByType = (data) => {
  return axios({
    method: 'post',
    url: '/authcenter/ugroup/invite',
    data,
  });
};

export const ApiGetCountry = () => {
  return axios({
    method: 'get',
    url: '/show/dictionary/getCountry',
  });
};

export const ApiGetCity = (countryId) => {
  return axios({
    method: 'get',
    url: `/show/dictionary/getCity?countryId=${countryId}`,
  });
};
