<template>
  <el-form :model="user" :rules="rules" class="clearfix" ref="form">
    <el-form-item prop="name"
      :label="$t('profile.info.user_name') + ':'">
      <el-input
        v-model="user.name"
        :placeholder="$t('profile.info.user_name')"
        @change="validateFormField('name', $event)" />
    </el-form-item>

    <el-form-item prop="department"
      :label="$t('profile.info.department') + ':'">
      <el-input
        v-model="user.department"
        :placeholder="$t('profile.info.department')"
        :disabled="user.roleId !== '1'"
        @change="validateFormField('department', $event)" />
    </el-form-item>
  </el-form>
</template>

<script>
import validator from '@/utils/validators/rules-validation';
import { mixinValidateFormField } from '@/mixins/form-validation';

export default {
  props: {
    user: {
      type: Object,
      required: true,
    },
  },
  watch: {
    // FIXME: 不知道为何el-input触发不了视图更新，加上这个就可以了
    user: {
      handler(usr) {
        this.usr = usr;
      },
      immediate: true,
    },
  },
  mixins: [mixinValidateFormField],
  data() {
    return {
      usr: {},
      rules: {
        name: [
          validator.spaceTrim(),
          // validator.charTypeNormal1(),
          ...validator.charLengthRange(undefined, 25),
        ],
        department: [
          validator.spaceTrim(),
          ...validator.charLengthRange(3, 25),
        ],
      },
    };
  },
};
</script>

<style lang="scss" scoped>
.el-form {
  .el-form-item {
    float: left;
    width: 420px;
    margin: 0 120px 0 0;
  }
  /deep/ .el-form-item__label {
    float: none;
    font-size: 14px;
    font-weight: 400;
    color: #7f7f7f;
  }
  /deep/ .el-form-item__content {
    font-size: 16px;
  }
}
</style>
