<template>
  <el-dialog
    :visible.sync="visible"
    :title="$t('profile.info.upload_profile_picture')"
    width="550px">
    <div class="cropper-box">
      <vue-cropper v-bind="option" ref="cropper" />
    </div>

    <!-- 分割线 -->
    <el-divider />

    <!-- 点击按钮区域 -->
    <div class="button-group">
      <upload-area
        :draggable="false"
        accept=".gif, .jpg, .jpeg, .png"
        @upload="handleUploadFile">
        <el-button>{{ $t('profile.info.upload_picture') }}</el-button>
      </upload-area>
      <div class="submit-buttons">
        <el-button @click="close">{{ $t('public.dialog.cancel') }}</el-button>
        <el-button type="primary" @click="submit">{{ $t('public.dialog.confirm') }}</el-button>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import { VueCropper } from 'vue-cropper';

import UploadArea from '@/components/upload';

import mixinDialog from '@/mixins/dialog-basic';

export default {
  name: 'UploadAvatar',
  components: {
    VueCropper,
    UploadArea,
  },
  mixins: [mixinDialog],
  props: {
    img: {
      type: String,
      default: require('@/assets/img/header/default_avatar.png'),
    },
  },
  data() {
    return {
      option: {
        img: this.img, // 裁剪图片的地址
        full: true, // 输出原图比例截图
        outputType: 'png', // 裁剪生成额图片的格式
        canMove: true, // 能否拖动图片
        canMoveBox: true, // 能否拖动截图框
        autoCrop: true, // 是否默认生成截图框
        fixed: true, // 是否开启截图框宽高固定比例
      },
    };
  },
  watch: {
    img(value) {
      this.option.img = value;
    },
  },
  methods: {
    // 将生成的图片bolb对象传入父组件；关闭弹框；
    submit() {
      this.$refs.cropper.getCropData(data => {
        this.$emit('generate-blob', data);
        this.visible = false;
      });
    },
    // 处理本地上传的图片
    handleUploadFile(file) {
      const URL = window.URL || window.wekitURL;
      this.option.img = URL.createObjectURL(file);
    },
  },
};
</script>

<style scoped lang="scss">
.cropper-box {
  width: 430px;
  height: 430px;
  margin: 0 auto 20px;
}

.el-divider {
  position: absolute;
  width: 100%;
  z-index: 2;
  left: 0;
}

.button-group {
  display: flex;
  justify-content: space-between;
  margin-top: 70px;
  .submit-buttons {
    .el-button {
      width: 100px;
    }
  }
}
</style>
