<template>
  <div class="company-info-container">
    <h1 class="company-info-title">{{ $t('profile.info.company_info') }}</h1>
    <!-- 公司信息表单 -->
    <form-company-info :company="company" @validate-field="handleChangeCompanyInfo" />
    <!-- 上传公司LOGO组件 -->
    <upload-company-logo :url="company.logo" @upload="handleChangeCompanyInfo" />
  </div>
</template>

<script>
import { getCompanyInfo, updateCompanyInfo } from '@/api/company';
import FormCompanyInfo from './form/FormCompanyInfo';
import UploadCompanyLogo from './upload/UploadCompanyLogo';


export default {
  components: {
    FormCompanyInfo,
    UploadCompanyLogo,
  },
  data() {
    return {
      company: { logo: '' },
    };
  },
  created() {
    this.fetchCompanyInfo();
  },
  methods: {
    // 获取账号的公司信息
    async fetchCompanyInfo() {
      try {
        this.company = (await getCompanyInfo()).data;
      } catch (e) {
        this.$errHandler(e);
      }
    },
    // 修改公司信息
    async handleChangeCompanyInfo({ field, value }) {
      try {
        await updateCompanyInfo({
          [field]: value,
          id: this.company.id,
        });
      } catch (err) {
        this.$errHandler(err);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.company-info-container {
  padding: 40px 60px;
  border-top: 0.05px solid transparent;

  .company-info-title {
    margin-bottom: 20px;
    font-size: 24px;
  }
}
</style>
