<template>
  <div class="user-info">
    <!-- 用户头像和信息 -->
    <div class="user-info-brief">
      <div class="avatar-box">
        <BaseImage class="avatar" :src="this.userImage" />
        <div
          v-if="$can('system', 'OP0006')"
          class="avatar-veil"
          @click="$refs.dialogUploadAvatar.open()">{{ $t('profile.info.modify_avatar') }}</div>
      </div>

      <div class="brief-box">
        <p class="brief-email">
          <span>{{ user.mail || user.account }}</span>
          <i
            v-if="$can('system', 'OP0006')"
            class="iconfont icon-write"
            @click="handleChangeEmail" />
        </p>
        <p class="brief-expire">
          {{ $t('profile.info.days_remain', { days: user.daysRemaining }) }}
        </p>
        <p
          v-if="$can('system', 'OP0006')"
          class="brief-password"
          @click="handleChangePassword">{{ $t('profile.info.change_password') }}</p>
      </div>
    </div>

    <!-- 用户昵称和部门 -->
    <form-user-info :user="user" @validate-field="handleChangeUserInfo" />

    <!-- 上传头像弹框 -->
    <dialog-upload-avatar
      :img="userImage"
      ref="dialogUploadAvatar"
      @generate-blob="handleGenerateDate" />
  </div>
</template>

<script>
import i18n from 'i18next';
import BaseImage from '@/components/base/Image';
import { getUserInfo, updateUserInfo } from '@/api/account';
import uploadBase64ToOSS from '@/utils/oss/uploadBase64ToOSS';
import { getContextUser } from '../../../../loaders/context';
import { updateUser } from '../../../../loaders/user';
import FormUserInfo from './form/FormUserInfo';
import DialogChangePassword from './dialog/DialogChangePassword';
import DialogUploadAvatar from './dialog/DialogUploadAvatar';
import DialogVerifyEmail from './dialog/DialogVerifyEmail';


export default {
  components: {
    BaseImage,
    FormUserInfo,
    DialogUploadAvatar,
  },
  computed: {
    /** @return { Item<User> } */
    user() {
      return getContextUser();
    },
    /** @return { string } */
    userImage() {
      return this.user.headImg || require('@/assets/img/header/default_avatar.png');
    },
  },
  methods: {
    modifyUserProfile(info) {
      return updateUser(info);
    },
    // 修改用户信息
    async handleChangeUserInfo({ field, value }) {
      try {
        await updateUserInfo({ [field]: value });
        this.modifyUserProfile({ [field]: value });
      } catch (err) {
        this.$errHandler(err);
      }
    },
    // 处理截取头像图片的bolb事件
    async handleGenerateDate(base64) {
      try {
        // 上传到OSS
        const path = await uploadBase64ToOSS(base64, 'USER_IMG');
        // 调用修改用户信息接口
        await updateUserInfo({ headImg: path });
        // 更新上传的头像的url到用户对象中
        const avatar = (await getUserInfo()).data.headImg;
        this.modifyUserProfile({ headImg: avatar });
      } catch (err) {
        this.$errHandler(err);
      }
    },
    async handleChangePassword() {
      await this.$Dialog.dialogify({
        is: DialogChangePassword,
      }, {
        props: {
          'close-on-click-modal': false,
          title: i18n.t('profile.info.change_password'),
          width: '540px',
          center: true,
        },
      });
    },
    async handleChangeEmail() {
      const res = await this.$Dialog.dialogify({
        is: DialogVerifyEmail,
        props: {
          user: this.user,
        },
      }, {
        props: {
          title: i18n.t('profile.info.change_link_email'),
          'close-on-click-modal': false,
          width: '440px',
          center: true,
        },
      });
      if (typeof res === 'string') this.submit(res);
    },
    async changeUserInfo(key, value, value2) {
      try {
        const info = { [key]: value };
        await updateUserInfo(info);
        if (!value2) {
          this.modifyUserProfile(info);
        } else this.modifyUserProfile({ [key]: value2 });
      } catch (e) {
        this.$errHandler(e);
      }
    },
    submit(mail) {
      this.modifyUserProfile({
        mail,
      });
      this.$store.dispatch('company/setCompany');
    },
  },
};
</script>

<style lang="scss" scoped>
.user-info {
  height: 300px;
  padding: 35px 53px;

  .user-info-brief {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }
}

.avatar-box {
  position: relative;
  width: 100px;
  height: 100px;
  margin-right: 24px;
  border-radius: 50%;
  overflow: hidden;

  &:hover .avatar-veil {
    display: flex;
  }
  .avatar-veil {
    display: none;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    cursor: pointer;
    transition: all linear 300ms;
  }
}

.brief-box {
  .brief-email {
    font-size: 21px;
    font-weight: 600;

    .iconfont {
      margin-left: 15px;
      font-size: 22px;
      color: #848b97;
      cursor: pointer;
    }
  }
  .brief-expire {
    margin: 10px 0;
    font-size: 13px;
    color: #7f7f7f;
  }
  .brief-password {
    cursor: pointer;
    color: #2878ff;
  }
}
</style>
