<template>
  <div>
    <form-change-password @validate="handleValidateFormSuccess" ref="formChangePassword" />

    <div class="flex-center" style="margin-top: 30px;">
      <el-button @click="$emit('cancel')">{{ $t('public.dialog.cancel') }}</el-button>
      <el-button type="primary" @click="$refs.formChangePassword.validateForm()">
        {{ $t('public.dialog.confirm') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import { ApiModifyPassword } from '@/api/profile';
import FormChangePassword from '../form/FormChangePassword';


export default {
  components: { FormChangePassword },
  methods: {
    // 修改密码
    async handleValidateFormSuccess({ oldPass, pass }) {
      try {
        await ApiModifyPassword({ pass: oldPass, newPass: pass });
        this.$refs.formChangePassword.resetFileds();
        this.$emit('confirm');
      } catch (err) {
        this.$errHandler(err);
      }
    },
  },
};
</script>
